::-webkit-scrollbar {
    width: 12.5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: black;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    height: 2px;
     
    background: #ff0000;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #990000; 
}


